* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  min-height: 100vh;
  background-color: whitesmoke;
}

.App h1 {
  font-size: 75px;
  margin: 25px;
}

.input {
  width: 400px;
  padding: 10px;
  border: 1.5px solid #000;
  outline: none;
  border-radius: 2.5px;
}

.select {
  padding: 10px;
  margin-left: 10px;
}

.equals {
  font-size: 50px;
  font-weight: bold;
}

@media (max-width: 770px) {
  .input {
    width: 200px;
  }
}
